// HELP SEARCH
.help-search {
  height: 25.5rem;
  overflow: hidden;
  white-space: nowrap;

  @media #{$sm} {
    height: 35.5rem;
  }

  &__container {
    position: relative;
    height: 100%;
    width: 100%;

    @include flex($direction: column, $align:center);
    
    .text {
      @include position(absolute, $top: 20%);
      z-index: 1;
      color: white;

      h2 {
        font-size: 1.5rem;
        @media #{$sm} {font-size: 2rem;
        }
      }
    }

    //container
    .image {
      @include position(absolute, $top: 0, $left: 0);
      width: 100%;
      height: 100%;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
}
